body, input { color: #757575; }

h5 {
  margin-top: 0;
}

main { overflow: hidden; }

textarea,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
	-webkit-appearance: none;
	border-radius: 0;
}
input[type=text] { font-size: 1em; }
input[type=submit] { font-weight: bold; }

input[type="submit"]:not([disabled]):hover { color: #000; }
input[type="submit"]:not([disabled]):focus{ -webkit-transform: translateY(0.125rem); -ms-transform: translateY(0.125rem); transform: translateY(0.125rem); }

ul {
	margin: 0;
	padding: 0;
}

.App { margin: 1em; }

.box {
	border: 2px solid #fbebcf;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	float: left;
	font-size: 1em;
	height: 200px;
	margin-bottom: .5em;
	min-width: 150px;
	padding: 1em;
	overflow-y: scroll;
	width: 100%;
}

.box h5 {
    color: #79510c;
	font-size: 1em;
	margin-bottom: .75em;
	text-transform: uppercase;
}

.disNon { display: none; }
.disVis { display: visible; }

.input-line { width: 80%; }

.marg { margin: .75em; }

.menu-block {
	border: 2px solid #fbebcf;
	overflow: hidden;
}

.menu-block h2 {
	background-color: #fbebcf;
	margin: 0;
	padding: .25em 0 .25em .75em;
}

.menu-block h2 input { background-color: #fbebcf; }

.c-top-right {
	position: absolute;
	top: .5em;
	right: .5em;
}

.contrast { color: #eeaa32; }

.inl { display: inline; }
.inl-b { display: inline-block; }

.left { float: left; }

.low-c { text-transform: lowercase; }

.right { float: right; }

input[type=submit]:not(disabled).featured { color: red; }
.neutral {}

@media (min-width: 450px) and (max-width: 600px) {
	.box {
		margin-bottom: 1%;
		width: 49.5%;
	}
	.box:nth-child(2n+1) { margin-right: 1%; }
}

@media (min-width: 600px) and (max-width: 800px) {
	.box {
		margin-bottom: 2%;
		width: 32%;
	}
	.box:not(:nth-child(3n)) { margin-right: 2%; }
}

@media (min-width: 800px) and (max-width: 1000px) {
	.box {
		margin-bottom: 1.333%;
		width: 24%;
	}
	.box:not(:nth-child(4n)) { margin-right: 1.333%; }
}

@media (min-width: 1000px) {
	.box {
		margin-bottom: 1.25%;
		width: 19%;
	}
	.box:not(:nth-child(5n)) { margin-right: 1.25%; }
}



