ul.list-box {
	font-size: .8em;
	list-style-type: none;
	margin-bottom: .5em;
	word-wrap: break-word;
}

.list-box li {
	background-color: #fff;
	border-bottom: 1px solid #eee;
	padding: .25em 0;
	margin-bottom: .2em;
}

.list-box li:hover, .list-box li:hover input { background-color: #fbebcf; }
.list-box li input { visibility: hidden; font-size: .75em; }
.list-box li:hover input, .list-box li:active input, .list-box li input[name=featured]  { cursor: pointer; visibility: visible; }

.list-box input.button {
	border: 2px solid #f9f5ee;
	margin: .5em auto;
	text-align: center;
}



